@import url('https://fonts.googleapis.com/css?family=Creepster');

.container {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.content {
  color: white;
  text-align: center;
}

.title {
  font-size: 2.5em;
  font-family: 'Creepster', cursive;
}

.shuffle-button {
  margin-top: 25px;
  padding: 10px 10px;
  width: 50%;
  border-radius: 4px;
  color: black;
  background-color: white;
  text-decoration: none;
  display: inline-block;
}

.shuffle-button:hover {
  background-color: #ccc;
  color: black;
}

.github {
  position: absolute;
  top: 10px;
  right: 10px;
}

.photo-attribution {
  position: absolute;
  bottom: 10px;
  left: 10px;
  font-size: 12px;
  color: white;
  text-decoration: none;
}

.photo-attribution:hover {
  color: #ccc;
}



